:root {
    --backMenu: rgb(69, 69, 66);
    --backMenu_hover: rgb(59, 59, 57);
    --backMenu_hover_select_active: rgb(47, 47, 46);
    --font-color: rgb(219, 219, 183);
    --font_size: 20px;
    --border_bottom: rgb(37, 37, 37);
    --drop-menu-width: 5px;
    --drop-menu-hover: rgb(17, 17, 17);
    --scroll-width: 10px;
    --scroll-track-color: rgb(60, 60, 59);
    --scroll-thumb-color: rgb(125, 125, 106);

}

.t-over {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.host-menu {
    cursor: default;
    max-height: 100%;

    background: var(--backMenu);
    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10 and IE 11 */
    user-select: none; /* Standard syntax */
}

.drop-menu {

    background: var(--backMenu);
    width: var(--drop-menu-width);
    cursor: col-resize;
}

.drop-menu:hover {
    background: var(--drop-menu-hover);
}

.selected-drop-menu {
    background: var(--drop-menu-hover);;
}

.tree-head {


}

.tree-body {
    overflow-x: hidden;
    overflow-y: auto;
}

.tree-footer {


}

.button-toggle-menu {
    position: absolute;
    left: 5px;
    bottom: 10px;
    cursor: pointer;
}

.button-toggle-menu:hover {

    border: 1px #222222 solid;
    border-radius: 5px;
}

.tree-one {
    display: none;
    text-align: center;
    color: var(--font-color);
    cursor: pointer;
    margin: 2px;
}

.tree-one:hover {
    background: var(--backMenu_hover);
}

.tree-menu-item {
    font-size: var(--font_size);
    cursor: pointer;

    padding: 10px 0 10px 0;


    color: var(--font-color);
    border-bottom: 1px solid var(--border_bottom);

}

.inner-div-a {
    display: flex;
    width: 100%;

    align-items: center;
}

.tree-menu-item:hover {
    background: var(--backMenu_hover);
}

.tree-menu-item:active {
    background: var(--backMenu_hover_select_active);
}

.select-tree-item {
    background: var(--backMenu_hover_select_active)
}


a[data-a-tree] {
    text-decoration: none;
    display: block;
}


[role=navigation]::-webkit-scrollbar {
    width: var(--scroll-width);
}

[role=navigation]::-webkit-scrollbar-track {
    background-color: var(--scroll-track-color);
}


[role=navigation]::-webkit-scrollbar-thumb {
    background-color: var(--scroll-thumb-color);

}


/*___________content item menu______________*/
.tree-menu-item-icon-tree {
    display: flex;
    align-items: center;
}

.tree-menu-item-left-image {
    padding: 0 15px 0 2px;
    display: flex;
    align-items: center;

}

.tree-menu-item-text {
    width: 100%;
    padding-left:5px;
    align-items: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;




}
.tree-menu-check-host {


    align-items: center;
}

.tree-menu-item-right-image {
    display: flex;
    align-items: center;

}
input[type=checkbox] {
    transform: scale(1.5);
}
.over-menu{
    position: absolute;
    width: 300px;
    height: fit-content;
    background: var(--backMenu);
    box-shadow: rgba(50, 50, 93, 0.25) 0 50px 100px -20px, rgba(0, 0, 0, 0.3) 0 30px 60px -30px, rgba(10, 37, 64, 0.35) 0 -2px 6px 0 inset;
}








